"use client";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Label } from "@radix-ui/react-label";
import { RadioGroup, RadioGroupItem } from "@radix-ui/react-radio-group";
import React, { useState } from "react";

const index = () => {
  const [selectedValue, setSelectedValue] = useState("exclusive");

  const bookingOptions = [
    {
      id: "single",
      value: "single",
      title: "Single-Slot Booking",
      description:
        "The radiographer operates one scanner for you, but may manage up to 2 more for other organizations simultaneously",
    },
    {
      id: "exclusive",
      value: "exclusive",
      title: "Exclusive Booking",
      description:
        "The radiographer works only for your organization, operating up to 3 scanners simultaneously as needed",
    },
  ];

  const handleValueChange = (value: string) => {
    setSelectedValue(value);
    // Optional: Add your alert/toast here if needed
  };

  return (
    <Card className="w-full max-w-md border-none shadow-none">
      <CardHeader className="px-0 pt-0">
        <CardTitle className="text-lg font-semibold">
          What type of booking do you require?
        </CardTitle>
      </CardHeader>
      <CardContent className="px-0 pb-0">
        <RadioGroup
          value={selectedValue}
          onValueChange={handleValueChange}
          className="space-y-4"
        >
          {bookingOptions.map((option) => (
            <div
              key={option.id}
              className={`flex items-start space-x-4 p-4 rounded-lg border transition-all
                ${
                  selectedValue === option.value
                    ? "border-primary bg-primary/5"
                    : "border-border hover:bg-accent/50"
                }`}
            >
              <RadioGroupItem
                value={option.value}
                id={option.id}
                className="mt-0.5" // Adjusts radio alignment
              />
              <Label
                htmlFor={option.id}
                className="flex flex-col space-y-1.5 cursor-pointer"
              >
                <span className="font-medium text-sm leading-none">
                  {option.title}
                </span>
                <span className="text-sm text-muted-foreground font-normal">
                  {option.description}
                </span>
              </Label>
            </div>
          ))}
        </RadioGroup>

        <div className="flex justify-between mt-8">
          <Button variant="outline" className="px-6">
            Cancel
          </Button>
          <Button className="px-6">Next â†’</Button>
        </div>
      </CardContent>
    </Card>
  );
};

export default index;
